namespace $ { export class $mol_perf_render extends $mol_viewer {

	/// title \$mol
	title() {
		return "$mol"
	}

	/// titler $mol_viewer 
	/// 	tagName \h2
	/// 	childs / < title
	@ $mol_mem()
	titler( next? : any , prev? : any ) {
		return new $mol_viewer().setup( obj => { 
			obj.tagName = () => "h2"
			obj.childs = () => [].concat( this.title() )
		} )
	}

	/// runnerLabel @ \Run
	runnerLabel() {
		return this.localizedText( "runnerLabel" )
	}

	/// eventRun null
	@ $mol_mem()
	eventRun( next? : any , prev? : any ) {
		return ( next !== void 0 ) ? next : <any> null
	}

	/// runner $mol_clicker_major 
	/// 	childs / < runnerLabel 
	/// 	eventClick > eventRun
	@ $mol_mem()
	runner( next? : any , prev? : any ) {
		return new $mol_clicker_major().setup( obj => { 
			obj.childs = () => [].concat( this.runnerLabel() )
			obj.eventClick = ( next? , prev? ) => this.eventRun( next , prev )
		} )
	}

	/// head / 
	/// 	< titler 
	/// 	< runner
	head() {
		return [].concat( this.titler() , this.runner() )
	}

	/// header $mol_viewer childs / < head
	@ $mol_mem()
	header( next? : any , prev? : any ) {
		return new $mol_viewer().setup( obj => { 
			obj.childs = () => [].concat( this.head() )
		} )
	}

	/// rows /
	rows() {
		return [] as any[]
	}

	/// lister $mol_lister rows < rows
	@ $mol_mem()
	lister( next? : any , prev? : any ) {
		return new $mol_lister().setup( obj => { 
			obj.rows = () => this.rows()
		} )
	}

	/// contenter $mol_scroller childs / < lister
	@ $mol_mem()
	contenter( next? : any , prev? : any ) {
		return new $mol_scroller().setup( obj => { 
			obj.childs = () => [].concat( this.lister() )
		} )
	}

	/// childs / 
	/// 	< header 
	/// 	< contenter
	childs() {
		return [].concat( this.header() , this.contenter() )
	}

} }

namespace $ { export class $mol_perf_render_row extends $mol_viewer {

	/// selected false
	@ $mol_mem()
	selected( next? : any , prev? : any ) {
		return ( next !== void 0 ) ? next : false
	}

	/// heightMinimal 24
	heightMinimal() {
		return 24
	}

	/// attr * mol_perf_render_row_selected < selected
	attr() {
		return $mol_merge_dict( super.attr() , {
			"mol_perf_render_row_selected" : ()=> <any> this.selected() ,
		} )
	}

	/// eventToggle null
	@ $mol_mem()
	eventToggle( next? : any , prev? : any ) {
		return ( next !== void 0 ) ? next : <any> null
	}

	/// event * click > eventToggle
	event() {
		return $mol_merge_dict( super.event() , {
			"click" : ( next? : any , prev? : any )=> <any> this.eventToggle( next , prev ) ,
		} )
	}

	/// label \
	label() {
		return ""
	}

	/// bar $mol_viewer childs / < label
	@ $mol_mem()
	bar( next? : any , prev? : any ) {
		return new $mol_viewer().setup( obj => { 
			obj.childs = () => [].concat( this.label() )
		} )
	}

	/// childs / < bar
	childs() {
		return [].concat( this.bar() )
	}

} }

